<template>
  <div class="shopcart">
    <navigation-bar @onLeftClick="goToBack" pageName="购物车">
      <template v-slot:nav-right>
        <div class="rightTitle" :class="{active:bottomBar==1}" @click="toManageCart">编辑</div>
        <div class="rightTitle" :class="{active:bottomBar==2}" @click="allReady">完成</div>
      </template>
    </navigation-bar>
    <!--显示购物车-->
    <div class="cartContent" ref="cartContent">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">

        <div class="cartContentBox">
          <!--店铺-->
          <div class="goodsCard" v-for="(shopItem, i) in lists">
            <div class="shopCard">
              <img :src="shopItem.selectFlag==0?img.beforClick:img.afterclick" v-show="bottomBar==2"
                @click="checkShop(i)">
              <img :src="shopItem.checkedShop==false?img.beforClick:img.afterclick" v-show="bottomBar==1"
                @click="checkShop(i)">
              <div :class="themeCurrent == '1' ? 'shopName-l' : 'shopName'" @click="toShop(shopItem.shopCode)">
                {{shopItem.shopName}}</div>
              <img class="shopSelect" :src="img.arrowShop">
              <div :class="themeCurrent == '1' ? 'shopDeliverName-l' : 'shopDeliverName'"
                v-if="shopItem.deliveryBaseLine!=0" @click="toShop(shopItem.shopCode)">
                满{{shopItem.deliveryBaseLine}}包邮</div>
            </div>
            <div class='actListBox' v-for="(item, j) in shopItem.actList">
              <div class="line" v-if="j!=0"></div>
              <div class="actBox" v-if="item.actCode">
                <div class="actMsgBox">
                  <div class="actTitle" v-if="item.actType==0||item.actType==1">满减</div>
                  <div class="actMsg" v-if="item.actType==0||item.actType==1">
                    {{item.actDesc}}</div>
                  <div class="actTitle" v-if="item.actType==2">打包</div>
                  <div class="actMsg" v-if="item.actType==2">{{item.actDesc}}</div>
                </div>
                <div class="actMoreBox" @click.stop="goActList(item)">
                  <div class="actMore">去凑单</div>
                  <img class="actMoreImg" src="@/assets/images/themeImg/icon_green.png" alt="">
                </div>
              </div>
              <van-swipe-cell :disabled="bottomBar==1?true:false" class="goodsDes"
                v-for="(message, index) in item.skuList">

                <div class="secksellBox" v-if="message.seckillFlag==0||message.seckillFlag==1||message.seckillFlag==4">
                  <div class="secksellMsgBox">
                    <div class="secksellTitle">秒杀</div>
                    <div class="secksellMsg" v-if="message.seckillFlag==0||message.seckillFlag==4">该商品距离秒杀结束仅剩</div>
                    <div class="secksellMsg" v-if="message.seckillFlag==1">该商品距离秒杀开始还有</div>
                    <van-count-down :auto-start="true" class="secksellTime" :time="message.time"
                      @finish="countDownFinish" />
                  </div>
                  <div class="secksellMoreBox" @click="goSeckSellList(message)">
                    <div class="secksellMore">去逛逛</div>
                    <img class="secksellMoreImg" src="@/assets/images/home/property_payment/more_red.png" alt="">
                  </div>
                </div>

                <div class="goodsDetailBox">
                  <div class="selectBox">
                    <img class="selectBtn" :src="message.selectFlag==0?img.beforClick:img.afterclick"
                      v-show="bottomBar==2" @click="checkSku(i,j,index)">
                    <img class="selectBtn" :src="message.checkedSku==false?img.beforClick:img.afterclick"
                      v-show="bottomBar==1" @click="checkSku(i,j,index)">
                  </div>
                  <div class="imgBox"><img class="goodsPic" :src="message.skuPicUrl" :onerror="defaultImg"
                      @click="turnToGoods(message)">
                    <div class="lowStock"
                      v-if="message.storeNumber<=message.warningValue&&message.storeNumber!=0&&message.spuStatus!='02'&&message.seckillFlag==3">
                      仅剩{{message.storeNumber}}件</div>
                    <span class="noStore"
                      v-if="message.storeNumber==0&&message.spuStatus!='02'&&message.seckillFlag==3">补货中</span>
                    <span class="noStore" v-if="message.spuStatus=='02'">已下架</span>
                  </div>

                  <div class="goodsDetails">
                    <div :class="themeCurrent == '1' ? 'goodsName-l' : 'goodsName'" @click="turnToGoods(message)">
                      {{message.spuName}}</div>
                    <div :class="themeCurrent == '1' ? 'attrValueContent-l' : 'attrValueContent'"
                      @click="turnToGoods(message)">{{message.attrValueContent}}</div>
                    <div class="goodsDetailBottom">

                      <div :class="themeCurrent == '1' ? 'skuPrice-l' : 'skuPrice'" @click="turnToGoods(message)">
                        ￥{{message.skuPrice}}</div>

                      <div class="stepperBox">
                        <img class="subtractBtn" src="../../assets/images/mall/mall_icon_subtract.png" alt="">
                        <img class="addBtn" src="../../assets/images/themeImg/icon_add.png" alt="">
                        <van-stepper class="stepperq" v-model="message.skuCount"
                          @blur="getCount(i,j,index,shopItem.shopCode)"
                          @overlimit='addNumBtn(message,shopItem.shopCode)' @plus='plusBtn(i,j,index,shopItem.shopCode)'
                          @minus='minusBtn(i,j,index,shopItem.shopCode)' :max="message.skuCountLimit"
                          :class="{stepperThemeL:themeCurrent=='1'}"></van-stepper>
                      </div>
                    </div>

                  </div>

                </div>
                <template slot="right">
                  <button class="addCollection" type="button" @click="addCollection(message)">收藏</button>
                  <button class="delectCart" type="button"
                    @click="delectCart(shopItem.shopCode,message.skuNo)">删除</button>
                </template>
                <div class="outSecksellBox" v-if="message.seckillFlag==4">
                  <div class="outSecksellTitle"> 限{{message.seckillLimit}}件 </div>
                  <div class="outSecksellMsg">秒杀商品限购{{message.seckillLimit}}件，超出将按原价购买该商品</div>
                </div>
              </van-swipe-cell>
            </div>

            <div class="shopTotalBox">
              <div class="deliveryBox">
                <div class="deliveryTitle">配送费</div>
                <div class="deliveryNum">¥{{shopItem.delivery}}</div>
              </div>
              <div class="deliveryBox">
                <div class="deliveryTitle">包装费</div>
                <div class="deliveryNum">¥{{shopItem.packFee}}</div>
              </div>
              <div class="line"></div>
              <div class="shopTotalBottomBox">
                <div>
                  <div class="deliveryDiffNum" v-if="shopItem.deliveryDiff>0">差{{shopItem.deliveryDiff}}起送</div>
                </div>

                <div class="shopTotalNumBox">
                  <div class="shopTotalNum">¥{{shopItem.skuTotal}}</div>
                  <div :class="shopItem.skuTotal==0?'shopGotoPayGray':'shopGotoPayRed'"
                    v-if="shopItem.deliveryDiff<=0||shopItem.skuTotal==0" @click="gotoShopPay(shopItem)">去结算</div>
                  <div class="collectBill" v-if="shopItem.deliveryDiff>0&&shopItem.skuTotal!=0"
                    @click="toShop(shopItem.shopCode)">去凑单</div>
                </div>
              </div>
            </div>
          </div>

        </div>
        <netWorkError v-if="ifNetWorkError"></netWorkError>
        <!-- 购物车里无商品时显示 -->
        <div class="noList" v-if="isLists==true">
          <img :src="img.noList" />
          <div>暂无商品</div>
        </div>
        <div ref="placeHolder" v-if="isShowPlaceHolder" style="height: 300px"></div>
      </van-pull-refresh>
    </div>


    <!--</div>-->
    <!--底部功能栏-->
    <!--编辑弹窗-->
    <div class="manage" :class="{active:bottomBar==2}" :style="{paddingBottom:this.$footBarHeight+'px'}">
      <div class="checkAllBox">
        <img class="editCheckAll" :src="editCheckedAll==false?img.beforClick:img.afterclick" @click="checkAll">
        <div :class="themeCurrent == '1' ? 'editCheckAllTxt-l' : 'editCheckAllTxt'">全选</div>
      </div>
      <!-- <van-checkbox class="checkAll" v-model="checkedAll" @click="checkAll" checked-color="#17B7BD" >全选</van-checkbox> -->
      <div class="manageButtonBox">
        <button :class="themeCurrent == '1' ? 'manageDelect-l' : 'manageDelect'" @click="allDelectCart">删除</button>
        <button :class="themeCurrent == '1' ? 'manageCollect-l' : 'manageCollect'" @click="allAddCollect">加入收藏</button>
      </div>
    </div>
    <van-submit-bar :class="{active:bottomBar==1, submitThemeL:themeCurrent=='1',isClickGoodClass:isClickGood}"
