      <img class="checkAll" :src="checkedAll==false?img.beforClick:img.afterclick" @click="checkAll">
      <div :class="themeCurrent == '1' ? 'checkAllTxt-l' : 'checkAllTxt'">全选</div>

      <!-- <van-checkbox class="checkAll" v-model="checkedAll" @click="checkAll" checked-color="#17B7BD">全选</van-checkbox> -->
    </van-submit-bar>
    <!--补货中弹窗-->
