    <div class="selloutBox" v-show="soldOut">
      <div class="selloutTxt">
        <div>补货中/已下架的商品</div>
        <div>不可结算哦</div>
      </div>
      <div class="iknowBtn" @click="iKnowBtn()">我知道了</div>
    </div>

  </div>
</template>

