<script>
  import { mapActions, mapState } from "vuex";
  import { Dialog } from 'vant';
  import Vue from 'vue';
  import netWorkError from '../../common/networkError/NetworkError'
  import NavigationBar from '@/common/currency/NavigationBar.vue';
  import { getUserShoppingCart, getUserShoppingCartNew, delectShoppingCart, addToCollection, addCollectList, updateGoodsNumAndAct, updateGoodsSelectAndAct, updateGoodsSelect ,checkOrderValidate} from "../../services/productInfoService";
  import { store } from "../../utils/storage";

  export default {
    data: function () {
      return {
        lists: [],
        skuNum: [],
        card: '',
        count: [],
        num: 0,
        soldOut: false,//以售罄彈窗
        checkedAll: false,//完成时全选
        isCheckNum: 0, //完成时选中店铺的数量
        skuCheckNum: 0,//完成时未选中商品的数量
        editCheckedAll: false,//编辑时全选
        editIsCheckNum: 0, //编辑时选中店铺的数量
        editSkuCheckNum: 0,//编辑时未选中商品的数量
        noStore: 0,//店铺中已售完的商品数量
        skuTotalNum: 0,//商品总数量
        result: [],
        checked: false,
        allPrice: 0,
        bottomBar: 2,
        isLoading: false,
        isClickGood: true,
        isLists: false,//购物车内是否存在商品
        img: {
          beforClick: require("@/assets/images/mall/check_default.png"),
          afterclick: require("@/assets/images/themeImg/check.png"),
          arrowShop: require("@/assets/images/mall/mall_icon_more.png"),
          noList: require("@/assets/images/default/no_shopping_img.png"),
        },
        setTopHeight: { 'padding-top': this.$stbHeight + 'px' },
        isShowPlaceHolder: false,
        ifNetWorkError: false,//判断是否网络异常
        themeCurrent: this.$curTheme,
        bindThemeL: this.themeCurrent == '1' ? true : false,
        showloading: null,
        defaultImg: 'this.src="' + require('../../assets/images/default/default_picture.png') + '"',
        isInitNow: false,//是否正在刷新购物车
      }
    },
    components: {
      netWorkError,
      NavigationBar
    },
    methods: {
      'init': function () {
        const data = {
          ownerCode: this.$userInfo.OWNER_CODE,
          areaCode: this.$userInfo.AREA_CODE,
          seckillExists: "1"
        }
        if (this.isInitNow) {
          return;
        }
        this.isInitNow = true;
        getUserShoppingCartNew(data).then(response => {
          this.isInitNow = false;
          this.ifNetWorkError = false;
          this.lists = response.data;
          if (response.data.length == 0 && this.bottomBar == 1) {
            this.bottomBar = 2;
          }

          this.checkSelect();//计算店铺选中及全选

          this.isLoading = false;
          this.isLists = response.data.length == 0 ? true : false;//判断购物车内是否存在商品
        }).catch(error => {
          //加载失败后，关闭加载等待的文字
          this.isInitNow = false;
          this.lists = [];
          this.ifNetWorkError = true;
          this.$toast("网络异常，请稍后重试");
          this.isLoading = false;
        });
      },
      'checkSelect': function () {


        var isCheckNum = 0;//店铺选中数量
        this.lists.map((item, i) => {
          item.checkedShop = false;
          item.selectFlag = 0;
          var shopSkuCheckNum = 0;
          var shopSkuCheckAndNocheckNum = 0;
          var shopSkuNum = 0;
          this.noStore = 0;

          item.actList.map((actItem) => {
            this.skuTotalNum += actItem.skuList.length;
            shopSkuNum += actItem.skuList.length;
            actItem.skuList.map((skuItem) => {

              if (skuItem.seckillFlag == 0 || skuItem.seckillFlag == 4) {
                var secondTime = skuItem.seckillEndTime - skuItem.serverTime;
                skuItem.time = secondTime * 1000;
              } else if (skuItem.seckillFlag == 1) {
                var secondTime = skuItem.seckillStartTime - skuItem.serverTime;
                skuItem.time = secondTime * 1000;
              }
              skuItem.time = secondTime * 1000;
              skuItem.checkedSku = false;
              if (!skuItem.selectFlag) {
                skuItem.selectFlag = 0;
              }
              //图片本地化存储
              Vue.prototype.$isHasLocalImage(skuItem.skuPicUrl, function (localImgUrl) {
                skuItem.skuPicUrl = localImgUrl;
              })
              if (skuItem.selectFlag == 1) {//包含补货中的商品
                shopSkuCheckNum += 1;
                shopSkuCheckAndNocheckNum += 1;
              }
              if ((skuItem.storeNumber == 0 && skuItem.seckillFlag == 3) || (skuItem.spuStatus == '02')) {//包含补货中的商品
                shopSkuCheckAndNocheckNum += 1;
              }
            });

          });
          if (shopSkuCheckAndNocheckNum == shopSkuNum) {
            isCheckNum += 1;
          }else
          {
            this.checkedAll = false;
          }
          if ((shopSkuCheckAndNocheckNum == shopSkuNum)&&shopSkuCheckNum>0) {
            item.selectFlag = 1;
          } else {
            item.selectFlag = 0;
          };

        })

        if (isCheckNum == this.lists.length) {//判断店铺是否全选中
          this.checkedAll = true;
        } else {
          this.checkedAll = false;
        }
        if (this.lists.length == 0) {
          this.checkedAll = false;
          this.editCheckedAll = false;
        }
        this.getTotal();
        this.$forceUpdate();
      },

      'onClickLeft': function () {
        this.$router.go(-1);//返回上一层
      },
      //    点击编辑
      'toManageCart': function () {

        if (this.lists.length == 0) {
          this.$toast("购物车内暂无商品哦，快去逛逛吧");
          return;
        }

        const _this = this;
        _this.bottomBar = 1;
        _this.cancelCheck();//初始化复选框状态
      },
      //    点击完成
      'allReady': function () {
        const _this = this;
        _this.bottomBar = 2;
        _this.cancelCheck();
        _this.getTotal();//重新计算商品价格
      },
      //        删除购物车内商品
      'delectCart': function (shop, sku) {
        const _this = this;
        const delectCart = {
          // ownerCode: "2019050214505753729",
          ownerCode: _this.$userInfo.OWNER_CODE,
          shopList: [{
            shopCode: shop,
            skuList: [sku],
          }]
        };
        delectShoppingCart(delectCart).then(function (response) {
          _this.init();
          // _this.cancelCheck();
          // _this.getTotal();
        });
      },
      'allDelectCart': function () {
        const _self = this;
        if (_self.lists.length == 0) {
          _self.$toast("您的购物车还没有商品呢");
        }
        const delectAllCart = {
          ownerCode: _self.$userInfo.OWNER_CODE,
          shopList: []
        };
        _self.lists.map((item, index) => {
          const skuList = [];
          item.actList.map((actItem) => {
            actItem.skuList.map((skuItem, i) => {
              if (skuItem.checkedSku == true) {
                skuList.push(skuItem.skuNo);
              }
            });
          });
          if (skuList.length != 0) {
            delectAllCart.shopList.push({
              shopCode: item.shopCode,
              skuList: skuList,
            });
          }
        });
        if (delectAllCart.shopList.length == 0) {
          _self.$toast("您还没有选中商品呢");
        } else {
          delectShoppingCart(delectAllCart).then(function (response) {
            _self.onRefresh();
          });
        }

      },
      addCollection(message) {//点击收藏切换收藏样式
        const _this = this;
        const savecollect = {
          collectType: '01',
          contentCode: message.spuCode,
          // userCode:'2019050214505753729'
          userCode: _this.$userInfo.OWNER_CODE,
        };
        addToCollection(savecollect).then(function (response) {
          if (response.data != null) {
            _this.$toast('添加收藏成功');
          } else if (response.resultCode == "50000") {
            _this.$toast('该商品已经添加收藏');
          } else {
            _this.$toast('添加收藏失败，请稍后重试');
          }
        });
      },
      'allAddCollect': function () {
        const _this = this;
        if (_this.lists.length == 0) {
          _this.$toast("您的购物车还没有商品呢");
        }
        const skuList = [];
        _this.lists.map((item, index) => {
          // const skuList = [];
          item.actList.map((actItem) => {
            actItem.skuList.map((skuItem, i) => {
              if (skuItem.checkedSku == true) {
                skuList.push({
                  contentCode: skuItem.spuCode,
                  collectType: "01",
                  userCode: _this.$userInfo.OWNER_CODE,
                })
              }
            });
          });
        });
        if (skuList.length != 0) {
          var data = {
            collectList: skuList
          };
          addCollectList(data).then(function (response) {
            if (response.resultCode == "0") {
              _this.$toast('添加收藏成功');
            }
          });
        } else {
          _this.$toast('您还没有选中商品呢');
        }
      },
      //      全选按钮
      'checkAll': function () {
        const _this = this;
        if (_this.bottomBar == 2)//完成状态
        {
          if (_this.checkedAll == true) {
            _this.checkedAll = false;
            _this.lists.map((item, index) => {
              item.selectFlag = 0;       //先控制每个店铺的复选框为false
              item.actList.map((actItem) => {
                actItem.skuList.map((skuItem) => {
                  skuItem.selectFlag = 0;     //控制每个商品的复选框为false
                });
              });
            });
            _this.updateGoodsSelectAndAct();
          } else if (_this.checkedAll == false) {
            if (_this.lists.length != 0) {
              _this.lists.map((item, index) => {
                item.actList.map((actItem) => {
                  actItem.skuList.map((skuItem) => {
                    if ((skuItem.storeNumber != 0 || skuItem.seckillFlag != 3) && skuItem.spuStatus != '02') {
                      skuItem.selectFlag = 1;
                    } else {
                      skuItem.selectFlag = 0;
                    }
                  });
                });
                item.selectFlag = 1;
              });
              _this.checkedAll = true;
              _this.updateGoodsSelectAndAct();
            } else {
              Dialog.alert({
                message: "购物车还没有商品哦，快去逛逛吧",
                confirmButtonText: "我知道了"
              }).then(() => {
                _this.checkedAll = false;
              });
            }
          }
          // _this.getTotal();
        }
        else if (_this.bottomBar == 1)//编辑状态
        {
          if (_this.editCheckedAll == true) {
            _this.editCheckedAll = false;
            _this.lists.map((item, index) => {
              item.checkedShop = false;       //先控制每个店铺的复选框为false
              item.actList.map((actItem) => {
                actItem.skuList.map((skuItem) => {
                  skuItem.checkedSku = false;     //控制每个商品的复选框为false
                });
              });
            });
          } else if (_this.editCheckedAll == false) {
            if (_this.lists.length != 0) {
              console.log(111111111111);
              _this.lists.map((item, index) => {
                item.actList.map((actItem) => {
                  actItem.skuList.map((skuItem) => {
                    skuItem.checkedSku = true;
                  });
                });
                item.checkedShop = true;
              });
              _this.editCheckedAll = true;
            } else {
              Dialog.alert({
                message: "您的购物车暂时还没有商品呢"
              }).then(() => {
                _this.editCheckedAll = false;
              });
            }
          }
          _this.getTotal();

        }

      },
      //      选中店铺
      'checkShop': function (i) {
        const _this = this;
        if (_this.bottomBar == 2)//完成状态
        {
          if (_this.lists[i].selectFlag == 0) {
            //店铺未选中状态
            _this.lists[i].actList.map((actItem) => {
              actItem.skuList.map((skuItem) => {
                if ((skuItem.storeNumber != 0 || skuItem.seckillFlag != 3) && skuItem.spuStatus != '02') {
                  skuItem.selectFlag = 1;
                } else {
                  skuItem.selectFlag = 0;
                }
              });
            });
            _this.updateGoodsSelectAndAct();
            // _this.lists[i].selectFlag = 1;

            // _this.lists.map((item) => {
            //   if (item.selectFlag == 1) {
            //     _this.isCheckNum += 1;
            //   }
            // });
            // if (_this.isCheckNum == _this.lists.length) {//判断店铺是否全选中
            //   _this.checkedAll = true;
            // } else {
            //   _this.checkedAll = false;
            // }

          } else if (_this.lists[i].selectFlag == 1) {
            //店铺未选中状态

            _this.lists[i].actList.map((actItem) => {
              actItem.skuList.map((skuItem) => {
                skuItem.selectFlag = 0;
              });
            });
            _this.updateGoodsSelectAndAct();

            // _this.lists[i].selectFlag = 0;
            // _this.checkedAll = false;
          }
          //计算结算金额
          // _this.getTotal();

        }
        else if (_this.bottomBar == 1)//编辑状态
        {

          if (_this.lists[i].checkedShop == false) {
            _this.editSkuCheckNum = 0;//选中商品数量置零 ?没有计算
            _this.editIsCheckNum = 0;//选中店铺数量置零
            // _this.lists[i].skuList.map((item) => {
            //   item.checkedSku = true;
            // })
            _this.lists[i].actList.map((actItem) => {
              actItem.skuList.map((skuItem) => {
                skuItem.checkedSku = true;
              });
            });
            _this.lists[i].checkedShop = true;
            _this.lists.map((item) => {
              if (item.checkedShop == true) {
                _this.editIsCheckNum += 1;
              }
            });
            if (_this.editIsCheckNum == _this.lists.length) {//判断店铺是否全选中
              _this.editCheckedAll = true;
            } else {
              _this.editCheckedAll = false;
            }
          } else if (_this.lists[i].checkedShop == true) {
            // _this.lists[i].skuList.map((item) => {
            //   item.checkedSku = false;
            // })
            _this.lists[i].actList.map((actItem) => {
              actItem.skuList.map((skuItem) => {
                skuItem.checkedSku = false;
              });
            });
            console.log(_this.lists[i].checkedShop + "改变状态");
            _this.lists[i].checkedShop = false;
            _this.editCheckedAll = false;
          }
        }
        _this.$forceUpdate();
      },
      //选中单个商品
      'checkSku': function (i, j, index) {//i代表店铺，index代表店铺内商品
        const _this = this;
        if (_this.bottomBar == 2)//完成状态
        {

          if ((_this.lists[i].actList[j].skuList[index].storeNumber != 0 || _this.lists[i].actList[j].skuList[index].seckillFlag != 3) && _this.lists[i].actList[j].skuList[index].spuStatus != '02') {
            // if (_this.lists[i].actList[j].skuList[index].selectFlag == 0) {
            //   _this.lists[i].actList[j].skuList[index].selectFlag = 1;
            //   _this.lists.map((item) => {
            //     var shopSkuCheckNum = 0;
            //     _this.noStore = 0;
            //     var shopSkuNum = 0;
            //     item.actList.map((actItem) => {
            //       shopSkuNum += actItem.skuList.length;
            //       actItem.skuList.map((skuItem) => {
            //         if (skuItem.selectFlag == 1 || (skuItem.storeNumber == 0 && skuItem.seckillFlag == 3)) {//包含补货中的商品
            //           shopSkuCheckNum += 1;
            //           if (skuItem.storeNumber == 0 && skuItem.seckillFlag == 3) {
            //             _this.noStore += 1;
            //           }
            //         }
            //       });
            //     });
            //     if (shopSkuCheckNum == shopSkuNum && _this.noStore < shopSkuNum) {
            //       item.selectFlag = 1;
            //       _this.isCheckNum += 1;
            //     } else {
            //       item.selectFlag = 0;
            //       _this.checkedAll = false;
            //     };
            //   });
            // } else {
            //   _this.lists[i].actList[j].skuList[index].selectFlag = 0;
            //   _this.lists[i].selectFlag = 0;
            //   _this.checkedAll = false;
            //   console.log(_this.lists[i].skuList[index].selectFlag + "$$$$$$$");
            // }
            // //        从数量上判断是否全选
            // if (_this.isCheckNum == _this.lists.length) {
            //   _this.checkedAll = true;
            // } else {
            //   _this.checkedAll = false;
            // }

            if (_this.lists[i].actList[j].skuList[index].selectFlag == 0) {
              _this.lists[i].actList[j].skuList[index].selectFlag = 1;
            } else {
              _this.lists[i].actList[j].skuList[index].selectFlag = 0;
            }
            _this.updateGoodsSelectAndAct();

          } else {
            _this.soldOut = true;
            _this.stop();
          }
          // _this.getTotal();

        }
        else if (_this.bottomBar == 1)//编辑状态
        {
          _this.editIsCheckNum = 0;//选中店铺数量置零
          if (_this.lists[i].actList[j].skuList[index].checkedSku == false) {
            _this.lists[i].actList[j].skuList[index].checkedSku = true;
            _this.lists.map((item) => {
              _this.editSkuCheckNum = 0;
              // _this.noStore = 0;
              var shopSkuNum = 0;
              item.actList.map((actItem) => {
                shopSkuNum += actItem.skuList.length;
                actItem.skuList.map((skuItem) => {
                  if (skuItem.checkedSku == true) {//包含补货中的商品
                    _this.editSkuCheckNum += 1;
                  }
                });
              });
              if (_this.editSkuCheckNum == shopSkuNum) {
                item.checkedShop = true;
                _this.editIsCheckNum += 1;
              } else {
                item.checkedShop = false;
                _this.editCheckedAll = false;
              };
            });
          } else {
            _this.lists[i].actList[j].skuList[index].checkedSku = false;
            _this.lists[i].checkedShop = false;
            _this.editCheckedAll = false;
          }
          //        从数量上判断是否全选
          if (_this.editIsCheckNum == _this.lists.length) {
            _this.editCheckedAll = true;
          } else {
            _this.editCheckedAll = false;
          }
        }

        _this.$forceUpdate();
      },
      //      跳转商品详情页
      turnToGoods(spuMessage) {
        console.log(spuMessage)

        if (spuMessage.spuStatus === '02') {
          this.$toast("很抱歉，该商品已下架");
        } else {
          //跳转秒杀商品详情
          if ((spuMessage.seckillFlag == 0 || spuMessage.seckillFlag == 4)) {
            this.$router.push({
              path: '/flashSaleGoods', query: {
                spuCode: spuMessage.spuCode,
                activitySpuId: spuMessage.oriActivitySpuId,
                goodsUrl: spuMessage.goodsPic
              }
            });
          } else {
            //跳转普通商品详情
            this.$router.push({
              name: 'goodsDetail', params: {
                spuCode: spuMessage.spuCode,
                goodsUrl: spuMessage.skuPicUrl
              }
            });
          }

        }
      },
      onRefresh() {
        this.checkedAll = false;
        this.allPrice = 0;
        this.init();
      },
      //更新商品数量
      'getCount': function (i, j, index, shopCode) {
        const _this = this;
        _this.lists[i].actList[j].skuList[index].skuCount = Number(_this.lists[i].actList[j].skuList[index].skuCount);
        if (_this.lists[i].actList[j].skuList[index].skuCount < 1) {
          _this.lists[i].actList[j].skuList[index].skuCount = 1;
        }
        if (_this.lists[i].actList[j].skuList[index].skuCount < _this.lists[i].actList[j].skuList[index].skuCountLimit) {
          // const data = {
          //   ownerCode: _this.$userInfo.OWNER_CODE,
          //   areaCode: _this.$userInfo.AREA_CODE,
          //   shopCode: shopCode,
          //   actCode: _this.lists[i].actList[j].actCode,
          //   skuList: _this.lists[i].actList[j].skuList,
          //   seckillExists: "1"
          // };
          // _this.showloading = store.get("showLoadingView");
          // if (_this.showloading) {
          //   _this.showloading(true);
          // }
          // updateGoodsNumAndAct(data).then(function (response) {
          //   // debugger
          //   if (response.resultCode == 0) {
          //     _this.lists[i].actList[j] = response.data.act;
          //     _this.checkSelect();//计算店铺选中及全选
          //     console.log("_this.lists[i].actList[j]");
          //     console.log(_this.lists[i].actList[j]);
          //     _this.$forceUpdate();
          //   }

          //   if (_this.showloading) {
          //     _this.showloading(false);
          //   }
          // }).catch(error => {
          //   if (_this.showloading) {
          //     _this.showloading(false);
          //   }
          // });
          _this.updateGoodsSelectAndAct();
        } else {
          _this.$toast("您所添加商品超过最大购买量了哦");
        }
      },

      //更新选中状态
      'updateGoodsSelectAndAct': function () {
        const _this = this;
        const data = {
          ownerCode: _this.$userInfo.OWNER_CODE,
          areaCode: _this.$userInfo.AREA_CODE,
          shopList: _this.lists,

          // seckillExists: "1"
        };
        _this.showloading = store.get("showLoadingView");
        if (_this.showloading) {
          _this.showloading(true);
        }
        updateGoodsSelectAndAct(data).then(function (response) {
          if (response.resultCode == 0) {
            _this.lists = response.data;
            _this.checkSelect();//计算店铺选中及全选

          }
          if (_this.showloading) {
            _this.showloading(false);
          }
        }).catch(error => {
          if (_this.showloading) {
            _this.showloading(false);
          }
        });

      },

      //更新选中状态
      'updateSelect': function () {
        const _this = this;
        const data = {
          ownerCode: _this.$userInfo.OWNER_CODE,
          areaCode: _this.$userInfo.AREA_CODE,
          shopList: _this.lists

        };
        updateGoodsSelect(data).then(function (response) {
        });

      },
      plusBtn(i, j, index, shopCode) {

        this.lists[i].actList[j].skuList[index].skuCount = Number(this.lists[i].actList[j].skuList[index].skuCount) + 1;
        this.getCount(i, j, index, shopCode);
      },
      minusBtn(i, j, index, shopCode) {

        this.lists[i].actList[j].skuList[index].skuCount = Number(this.lists[i].actList[j].skuList[index].skuCount) - 1;
        this.getCount(i, j, index, shopCode);
      },
      addNumBtn(numMessage, shopCode) {
        if (numMessage.skuCount == 1) {
          this.delectCart(shopCode, numMessage.skuNo);
        } else {
          this.$toast("您所添加商品超过最大购买量了哦")
        }
      },
      cancelCheck() {//初始化复选框状态
        const _this = this;
        _this.lists.map((item, index) => {
          item.checkedShop = false;       //先控制每个店铺的复选框为false
          item.actList.map((actItem) => {
            actItem.skuList.map((skuItem) => {
              skuItem.checkedSku = false;     //控制每个商品的复选框为false
            });
          });
        });
        _this.editCheckedAll = false;
      },
      //      计算商品总价
      'getTotal': function () {
        const _this = this;
        _this.allPrice = 0;
        _this.isClickGood = false;
        this.lists.map((item) => {
          // item.actList.map((actItem) => {
          //   actItem.skuList.map((skuItem) => {
          //     if (skuItem.selectFlag == 1) {
          //       _this.allPrice += (skuItem.skuPrice * 100) * skuItem.skuCount;
          //     }
          //   });
          //   if (actItem.autoid && actItem.actType == 2) {
          //     _this.allPrice -= actItem.off * 100;
          //   }
          //   if (actItem.autoid && (actItem.actType == 0 || actItem.actType == 1)) {
          //     _this.allPrice -= actItem.ruleReduction * 100;
          //   }
          // });
          _this.allPrice += item.skuTotal * 100;

          if (item.skuTotal != 0 && item.deliveryDiff > 0) {
            _this.isClickGood = true;
          }

        });
        //      判断结算栏是否置灰
        if (_this.allPrice == 0) {
          _this.isClickGood = true;
        }
      },


      countDownFinish() {
        this.onRefresh();
      },

      //    购物车商品结算
      payForCart() {
        if (this.isClickGood) {
          if (this.allPrice != 0) {
            this.$toast("有店铺未满足起送价哦");
          }
          return;
        }
        const _this = this;
        const settleAccountsList = [];
        _this.lists.map((item, index) => {//循环遍历店铺
          const skuList = [];

          item.actList.map((actItem) => {
            actItem.skuList.map((skuItem, i) => {
              if (skuItem.selectFlag == 1) {
                skuList.push({
                  activitySpuId: skuItem.activitySpuId,
                  spuCode: skuItem.spuCode,
                  spuName: skuItem.spuName,
                  skuNo: skuItem.skuNo,
                  attrValueContent: skuItem.attrValueContent,
                  skuPrice: skuItem.skuPrice,
                  skuCount: skuItem.skuCount, //数量
                  skuDelivery: 0,
                  skuTotal: skuItem.skuCount * skuItem.skuPrice,
                  skuPicUrl: skuItem.skuPicUrl,
                  // activitySpuId:_this.$route.params.activitySpuId
                })
              }
            });
          });
          if (skuList.length != 0) {
            settleAccountsList.push({
              shopName: item.shopName,
              shopCode: item.shopCode,
              skuList: skuList,
            })
          }
        });
        this.checkOrderIfValid(settleAccountsList);
        // const goodsData = {
        //   orderSource: "0",
        //   goodsList: settleAccountsList,
        //   num: Math.random() * 100
        // };
        // _this.$router.push({
        //   name: "orderPayNew",
        //   params: {
        //     'goodsData': goodsData
        //   }
        // });
        // console.log("传值" + JSON.stringify(goodsData));
      },

      //店铺结算
      gotoShopPay(shopItem) {
        var _this = this;
        var settleAccountsList = [];
        var skuList = [];
        shopItem.actList.map((actItem) => {
          actItem.skuList.map((skuItem, i) => {
            if (skuItem.selectFlag == 1) {
              skuList.push({
                activitySpuId: skuItem.activitySpuId,
                spuCode: skuItem.spuCode,
                spuName: skuItem.spuName,
                skuNo: skuItem.skuNo,
                attrValueContent: skuItem.attrValueContent,
                skuPrice: skuItem.skuPrice,
                skuCount: skuItem.skuCount, //数量
                skuDelivery: 0,
                skuTotal: skuItem.skuCount * skuItem.skuPrice,
                skuPicUrl: skuItem.skuPicUrl,
                // activitySpuId:_this.$route.params.activitySpuId
              })
            }
          });
        });

        if (skuList.length != 0) {
          settleAccountsList.push({
            shopName: shopItem.shopName,
            shopCode: shopItem.shopCode,
            skuList: skuList,
          })
          this.checkOrderIfValid(settleAccountsList);
          // var goodsData = {
          //   orderSource: "0",
          //   goodsList: settleAccountsList,
          //   num: Math.random() * 100
          // };
          // _this.$router.push({
          //   name: "orderPayNew",
          //   params: {
          //     'goodsData': goodsData
          //   }
          // });

        }


      },
      checkOrderIfValid(settleAccountsList) {
      let param = {
        ownerCode: this.$userInfo.OWNER_CODE,
        orderSource: "0",
        areaCode: this.$userInfo.AREA_CODE,
        shopList: settleAccountsList
      };
      checkOrderValidate(param)
        .then(res => {
          if (res.resultCode == 0) {
            this.gotoOrderPayPage(settleAccountsList);
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(error => {
          this.$toast("网络异常，请稍后重试");
        });
    },
    gotoOrderPayPage(settleAccountsList) {
      var goodsData = {
        orderSource: "0",
        goodsList: settleAccountsList,
        num: Math.random() * 100
      };
      this.$router.push({
        name: "orderPayNew",
        params: {
          goodsData: goodsData
        }
      });
    },

      //跳转秒杀列表
      goSeckSellList(message) {
        this.$router.push({
          name: 'flashSaleList',
          params: {
            firstTab: message.seckillFlag
          }
        })
      },
      // 再次购买跳转购物车
      // repurchase() {
      //   const repurchaseData = this.$route.params.data;
      //   console.log("走进了再次购买方法" + this.lists)
      //   this.lists.map((item, i) => {
      //     // item.checkedShop = false;
      //     item.skuList.map((skuItem, index) => {
      //       repurchaseData.map((repurchaseSpuCode, indexSpu) => {
      //         if (repurchaseSpuCode.spuCode == skuItem.spuCode) {
      //           this.checkSku(i, index);
      //           console.log(repurchaseSpuCode.spuCode + "大师傅大师傅" + skuItem.spuCode + "index" + index)
      //         }
      //       });
      //     });
      //   });
      // },
      // 跳转店铺s
      toShop(shopCode) {
        this.$router.push({ name: 'shopPage', params: { shopCode: shopCode } });
      },
      // 跳转活动页面
      goActList(item) {
        //跳转活动页面
        console.log(item)
        this.$router.push({
          name: 'reducedPackaging',
          params: {
            "typeVal": item.actType,
            "activityId": item.actCode
          }
        })
      },
      goToBack() {
        let backResult=this.$setRouterIndex();
        if(backResult){
           this.$router.go(-1);
        }
      },
      //    关闭弹窗
      iKnowBtn() {
        const _this = this;
        _this.soldOut = false;
        _this.move();
      },
      /***滑动限制***/
      stop() {
        var mo = function (e) { e.preventDefault(); };
        document.body.style.overflow = 'hidden';
        document.addEventListener("touchmove", mo, false);//禁止页面滑动
      },
      /***取消滑动限制***/
      move() {
        var mo = function (e) { e.preventDefault(); };
        document.body.style.overflow = '';//出现滚动条
        document.removeEventListener("touchmove", mo, false);
      },
      //键盘弹出事件
      moveInputIntoView(e) {
        console.log(this.isShowPlaceHolder);
        if (this.isShowPlaceHolder) {
          return;
        }
        this.isShowPlaceHolder = true;
        this.$nextTick(() => {
          console.log("scrollTop");
          var actualTop = document.activeElement.parentNode.parentNode.parentNode.parentNode.parentNode.parentNode.parentNode.offsetTop;
          this.$refs.cartContent.scrollTop = actualTop;
        });

      },
      //键盘收回事件
      resetInputIntoView(e) {
        var that = this;
        console.log("resetInputIntoView");
        setTimeout(function () {
          that.isShowPlaceHolder = false;
          that.$forceUpdate();
        }, 100);
        if (document.activeElement) {
          document.activeElement.blur();
        }
      },
    },

    mounted: function () {
      //添加键盘监听,让第一响应者滚动到键盘上方显示
      window.addEventListener("native.keyboardshow", this.moveInputIntoView);
      window.addEventListener("native.keyboardhide", this.resetInputIntoView);
      document.querySelector(".shopcart").addEventListener('touchmove', function (e) {
        if (document.activeElement)
          document.activeElement.blur();
      }, { passive: false });
      this.init();
    },
    beforeRouteLeave(to, from, next) {

      // this.updateSelect();
      //移除键盘监听,让第一响应者滚动到键盘上方显示
      window.removeEventListener("native.keyboardshow", this.moveInputIntoView);
      window.removeEventListener("native.keyboardhide", this.resetInputIntoView);
      next();
    },

  }
</script>
